import React, { Component } from 'react'
import { Table, Switch, Divider } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'

class FlightStatus extends Component {
  state = {
    form: [
      {
        key: 1,
        no: 1,
        statusEN: 'Cancelled',
        statusTH: 'ยกเลิก',
        timeStatus: 'Not Display',
        display: true,
        updateDate: '1 August 2018 08:33',
        cause: [
          {
            statusEN: 'Delayed by waitting for origin flight',
            statusTH: 'ล่าช้าเนื่องจากรอเที่ยวบินต้นทาง',
            display: true,
          },
          {
            statusEN: 'Delayed by waitting for origin flight',
            statusTH: 'ล่าช้าเนื่องจากรอเที่ยวบินต้นทาง',
            display: true,
          },
        ]
      }, {
        key: 2,
        no: 2,
        statusEN: 'Cancelled',
        statusTH: 'ยกเลิก',
        timeStatus: 'Not Display',
        display: true,
        updateDate: '1 August 2018 08:33',
        cause: [
          {
            statusEN: 'Delayed by waitting for origin flight',
            statusTH: 'ล่าช้าเนื่องจากรอเที่ยวบินต้นทาง',
            display: true,
          },
          {
            statusEN: 'Delayed by waitting for origin flight',
            statusTH: 'ล่าช้าเนื่องจากรอเที่ยวบินต้นทาง',
            display: true,
          },
        ]
      }
    ]
  }

  edit = (item) => {
    //const path = item ? ('/' + item.key) : ''
    //this.props.history.push('/airlinesetting/edit' + path)
  }
  add = () => { }

  render() {
    const { form } = this.state

    const columns = [
      {
        title: '#',
        key: 'no',
        dataIndex: 'no',
      }, {
        title: 'Flight Status (Eng)',
        dataIndex: 'statusEN',
        key: 'statusEN'
      }, {
        title: 'Flight Status (TH)',
        dataIndex: 'statusTH',
        key: 'statusTH'
      }, {
        title: 'Time of Status',
        dataIndex: 'timeStatus',
        key: 'timeStatus'
      }, {
        title: 'Display(On/Off)',
        dataIndex: 'display',
        key: 'display',
        render: display => <Switch checked={display} />
      }, {
        title: 'Update Date',
        dataIndex: 'updateDate',
        key: 'updateDate'
      }, {
        title: 'Edit',
        key: 'edit',
        render: item => (
          <button onClick={() => this.edit(item)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Delete',
        key: 'delete',
        render: () => (
          <button onClick={this.delete} type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button>
        )
      }
    ]

    const table = <Table
      style={{ minWidth: 800 }}
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      expandedRowRender={record => {
        return record.cause.map((l, i) => (
          <div className="form-inline mb-2" key={i}>
            <div className="col-md-1 text-right">{i + 1}<Divider type="vertical" /></div>
            <div className="col-md-4 text-left">ENG :{l.statusEN}</div>
            <div className="col-md-4 text-left">TH :{l.statusTH}</div>
            <div className="col-md-2 text-left">Display (On/Off) :{l.display ? 'On' : 'Off'}</div>
          </div>
        ))
      }}
      dataSource={form} />
    return (
      <div className="card card-body card-body-component">
        <div className="card card-body card-header-table">
          <div className="mb-3">
            <button type="button" onClick={this.add} className="btn btn-success">
              <FontAwesomeIcon icon="plus" /> Add Flight Status
          </button>
          </div>
          {table}
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(FlightStatus)
