import React, { Component } from 'react'
import './Dropdown.css'
import { Icon } from 'antd'
import { connect } from 'react-redux'

class Dropdown extends Component {
  state = {
    stateOptions: []
  }

  componentDidMount() {
    document.addEventListener('mousedown', this.handleClickOutside)
    this.setState({ stateOptions: [] })
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ stateOptions: [] })
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  handleClickOutside = event => {
    if (this.refs.wrapperRef && !this.refs.wrapperRef.contains(event.target)) {
      this.refs.ddl && this.refs.ddl.classList.remove("show")
      setTimeout(() => {
        if (this.refs.wrapperRef) this.setState({ stateOptions: [] })
      }, 200)
    }
  }

  toggleDropdown = () => {
    this.refs.ddl && this.refs.ddl.classList.toggle("show")
    this.refs.input && this.refs.input.focus()
    setTimeout(() => {
      if (this.refs.wrapperRef) this.setState({ stateOptions: this.props.options })
    }, 200)
  }

  filterFunction = () => {
    let input, filter, a, i
    input = this.refs.input
    filter = input.value.toUpperCase()
    let div = this.refs.ddl
    a = div.getElementsByTagName("li")
    for (i = 0; i < a.length; i++) {
      if (a[i].innerHTML.toUpperCase().indexOf(filter) > -1) {
        a[i].style.display = ""
      } else {
        a[i].style.display = "none"
      }
    }
  }

  onSelect = (data) => {
    this.refs.ddl && this.refs.ddl.classList.remove("show")
    setTimeout(() => {
      if (this.props.onSelect) this.props.onSelect(data)
    }, 200)
  }

  render() {
    const {
      options,
      selected,
      placeholder,
      language,
      className,
      btStyle,
      disabled,
    } = this.props
    const { stateOptions } = this.state
    const option = options.filter(x => x.id === selected)[0]
    const searchText = language === 'th' ? 'ค้นหา' : 'search'
    return (
      <div className="custom-dropdown" ref="wrapperRef">
        <button
          disabled={disabled}
          onClick={this.toggleDropdown}
          className={"dropbtn " + className + (disabled ? ' disabled' : '')}
          style={[btStyle, { backgroundColor: 'red' }]}
          title={option && option.text}>
          <span>
            {!option && placeholder}
            {option && option.text}
          </span>
        </button>
        <Icon className="icon" type="down" style={{ fontSize: '12px', color: '#66ccff' }} />
        <div ref="ddl" className="dropdown-content">
          <input type="text" placeholder={searchText} ref="input" onKeyUp={this.filterFunction} />
          {stateOptions.length === 0 && <div className="text-center">
            <Icon type="sync" spin />
          </div>}
          <select className="list" multiple="multiple">
            {stateOptions.map((data, i) => {
              return <option
                key={i}
                title={data.text}
                onClick={this.onSelect.bind(null, data)}>{data.text}</option>
            })}
          </select>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(Dropdown)
