import React, { Component } from 'react'
import moment from 'moment'
import { TimePicker } from 'antd'
import './AdvanceSearch.css'
import context from '../../utils/language'
import { connect } from 'react-redux'
import Render from '../../components/Renderer'

const timeFormat = 'HH:mm'

class AdvanceSearch extends Component {
  render() {
    const { show, dropdown, form, language } = this.props
    const name = context[language].search
    const optionsList = [
      { label: (name && name.arrival), value: '1' },
      { label: (name && name.departure), value: '2' }
    ]
    const style = show ? 'show' : 'hide'
    const template = [
      {
        type: 'rangepicker',
        name: 'range',
        label: name && name.betweenDate,
        language: 'th',
        value: form.range,
      }, {
        type: 'custom',
        label: name && name.betweenTime,
        component: <div className="form-row">
          <div className="col">
            <TimePicker
              name="startTime"
              onChange={(value, dateString) => {
                if (this.props.onChange) this.props.onChange({ name: 'startTime', value: dateString })
              }}
              defaultOpenValue={moment(form.startTime || '00:00', timeFormat)}
              format={timeFormat}
              placeholder={name && name.betweenTimePlaceholder}
              style={{ width: '100%' }}
            />
          </div>
          <div className="col-auto align-self-center">{' '}-{' '}</div>
          <div className="col">
            <TimePicker
              name="endTime"
              onChange={(value, dateString) => {
                if (this.props.onChange) this.props.onChange({ name: 'endTime', value: dateString })
              }}
              defaultOpenValue={moment(form.endTime || '00:00', timeFormat)}
              format={timeFormat}
              placeholder={name && name.betweenTimePlaceholder}
              style={{ width: '100%' }}
            />
          </div>
        </div>
      }, {
        type: 'select',
        name: 'callsign',
        label: name && name.callsign,
        value: form.callsign,
        mode: "multiple",
        placeholder: name && name.callsignPlaceholder,
        options: dropdown.traffTypeOptions
      }, {
        type: 'select',
        label: name && name.airType,
        name: 'airTypeID',
        mode: "multiple",
        placeholder: 'Select...',
        value: form.airTypeID,
        options: dropdown.airTypeOptions.map(x => ({
          value: x.airTypeID,
          text: x.airType
        })).filter((thing, index, self) =>
          index === self.findIndex((t) => (
            t.value === thing.value
          ))
        )
      }, {
        type: 'select',
        name: 'trafftype',
        label: name && name.trafftype,
        value: form.trafftype,
        mode: "multiple",
        placeholder: name && name.trafftypePlaceholder,
        options: dropdown.traffTypeOptions
      }, {
        type: 'select',
        name: 'destination',
        label: name && name.destination,
        value: form.destination,
        mode: "multiple",
        placeholder: name && name.destinationPlaceholder,
        options: dropdown.airPortOptions.map(x => ({
          value: x.airportID,
          text: x.airportName
        })).sort((a, b) => a.text.localeCompare(b.text))
      }, {
        type: 'checkall',
        label: 'All',
        name: 'checkInOut',
        value: form.checkInOut || ['1', '2'],
        options: optionsList
      }, {
        type: 'input',
        name: 'cardNo',
        label: name && name.cardNo,
        value: form.cardNo,
        placeholder: name && name.cardNoPlaceholder
      }
    ]
    return (
      <div className={"adv-search-container " + style}>
        <h5 className="text-secondary text-center p-2">{name && name.advanceSearch}</h5>
        <div className="row">
          {template.map((t, i) => (
            <div key={i} className="col-12 col-lg-6 form-inline form-row mb-2">
              <label className="col-12 col-sm-4 justify-content-end">{t.label} :</label>
              <div className="col col-xl-8">
                {Render(t, this.props.onChange)}
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

export default connect(mapStateToProps)(AdvanceSearch)
