import React, { Component } from 'react'
import { Table, Switch, Divider } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'

class AirType extends Component {
  state = {
    form: [
      {
        key: 1,
        no: 1,
        callsign: 'B-HOL',
        airline: 'NOK AIR',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        display: true,
        updateDate: '1 August 2018 08:33',
        list: [
          {
            airtype: 'B-747',
            grossWeight: '100000',
            pax: '300'
          },
          {
            airtype: 'B-748',
            grossWeight: '100000',
            pax: '300'
          },
          {
            airtype: 'B-749',
            grossWeight: '100000',
            pax: '300'
          }
        ]
      }, {
        key: 2,
        no: 2,
        callsign: 'B-HOL',
        airline: 'NOK AIR',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        display: true,
        updateDate: '1 August 2018 08:33',
        list: [
          {
            airtype: 'B-747',
            grossWeight: '100000',
            pax: '300'
          },
          {
            airtype: 'B-748',
            grossWeight: '100000',
            pax: '300'
          },
          {
            airtype: 'B-749',
            grossWeight: '100000',
            pax: '300'
          }
        ]
      }, {
        key: 3,
        no: 3,
        callsign: 'B-HOL',
        airline: 'NOK AIR',
        companyName: 'NOK AIRLINES PUBLIC CO LTD',
        display: true,
        updateDate: '1 August 2018 08:33',
        list: [
          {
            airtype: 'B-747',
            grossWeight: '100000',
            pax: '300'
          },
          {
            airtype: 'B-748',
            grossWeight: '100000',
            pax: '300'
          },
          {
            airtype: 'B-749',
            grossWeight: '100000',
            pax: '300'
          }
        ]
      }
    ]
  }

  edit = (item) => {
    //const path = item ? ('/' + item.key) : ''
    //this.props.history.push('/airlinesetting/edit' + path)
  }
  add = () => { }

  render() {
    const { form } = this.state

    const columns = [
      {
        title: '#',
        key: 'no',
        dataIndex: 'no',
      }, {
        title: 'Callsign',
        dataIndex: 'callsign',
        key: 'callsign'
      }, {
        title: 'Airline',
        dataIndex: 'airline',
        key: 'airline'
      }, {
        title: 'Owner',
        dataIndex: 'companyName',
        key: 'companyName'
      }, {
        title: 'Display(On/Off)',
        dataIndex: 'display',
        key: 'display',
        render: display => <Switch checked={display} />
      }, {
        title: 'Update Date',
        dataIndex: 'updateDate',
        key: 'updateDate'
      }, {
        title: 'Edit',
        key: 'edit',
        render: item => (
          <button onClick={() => this.edit(item)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Delete',
        key: 'delete',
        render: () => (
          <button onClick={this.delete} type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button>
        )
      }
    ]

    const table = <Table
      style={{ minWidth: 800 }}
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      expandedRowRender={record => {
        return record.list.map((l, i) => (
          <div className="form-inline mb-2" key={i}>
            <div className="col-md-1 text-right">{i + 1}<Divider type="vertical" /></div>
            <div className="col-md-2 text-left">Air Type :{l.airtype}</div>
            <div className="col-md-3 text-left">Gross Weight :{l.grossWeight}</div>
            <div className="col-md-2 text-left">Pax :{l.pax}</div>
          </div>
        ))
      }}
      dataSource={form} />
    return (
      <div className="card card-body card-body-component">
        <div className="card card-body card-header-table">
          <div className="mb-3">
            <button type="button" onClick={this.add} className="btn btn-success">
              <FontAwesomeIcon icon="plus" /> Add AirType
          </button>
          </div>
          {table}
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(AirType)
